:root{
  --color-primary: #34c94b;
  --color-black: #121212;
  --color-white: #ffffff;
}

*,
*::before, *::after{
  box-sizing: border-box;
}

*{
  margin:0;
}

html,body{
  height:100%;
}

body{
  font-family:'Poppons', sans-serif;
  line-height: 1.5;
  background-color: var(--color-black);
  color: var(--colo-white);
  overflow-x: hidden;
}

img,svg{
  display: block;
  max-width: 100%;
}

a,a:hover,a:focus,a:active{
  text-decoration: none;
  
}